@import url(https://netdna.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.css);
@import url(https://fonts.googleapis.com/css?family=Lato:400,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  /* --header-height: 56px; */
  --app-background: #e5e5e5;
}

.app-wrapper {
  padding: 1.375rem;
  margin-top: var(--header-height);
  background-color: #e5e5e5;
  background-color: var(--app-background);
  min-height: calc(100vh - var(--header-height));
}

:root {
  --white: #ffffff;
  --off-white: #eee;
  --main-background: #222222;
  --fallback-color: #16222a;
  --gradient-color: #3a6073;
  --dark-gray: #5b5b5b;
  --container-background-color: rgba(58, 63, 68, 0.5);
}

.main {
  /* Remember to use the other versions for IE 10 and older browsers! */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "lato", sans-serif;
  color: #ffffff;
  color: var(--white);
  background: #222222;
  background: var(--main-background);
  background: #16222a;
  background: var(--fallback-color); /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #16222a,
    #3a6073);
  background: linear-gradient(
    to top,
    var(--fallback-color),
    var(--gradient-color)
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.container__login {
  background: rgba(58, 63, 68, 0.5);
  background: var(--container-background-color);
  border-radius: 0.5rem;
  box-shadow: 0 1.5px 0 0 rgba(0, 0, 0, 0.1);
  width: 28rem;
  display: flex;
  flex-direction: column;
}

.logo {
  font-family: "museo-slab";
  font-size: 1.25rem;
  text-align: center;
  padding: 1.25rem 1.25rem 0;
  margin: 0;
}

.login-item {
  color: #ffffff;
  color: var(--white);
  padding: 1.5rem 1.5rem 0;
  margin: 1.25rem 1.25rem 0;
  border-radius: 0.5rem;
}

input {
  border: 0;
  color: inherit;
  font: inherit;
  margin: 0;
  outline: 0;
  padding: 0;
  transition: background-color 0.3s;
}

.user:before {
  content: "\f007";
  font: 0.8rem fontawesome;
  color: #5b5b5b;
  color: var(--dark-gray);
}

.lock:before {
  content: "\f023";
  font: 0.8rem fontawesome;
  color: #5b5b5b;
  color: var(--dark-gray);
}

.form input[type="password"],
.form input[type="text"],
.form input[type="submit"] {
  width: 100%;
}

.form-login label,
.form-login input[type="text"],
.form-login input[type="password"],
.form-login input[type="submit"] {
  border-radius: 0.25rem;
  padding: 1rem;
  color: #3a3f44;
}

.form-login label {
  background-color: #222222;
  background-color: var(--main-background);
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
}

.form-login input[type="text"],
.form-login input[type="password"] {
  background-color: #ffffff;
  background-color: var(--white);
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
.form-login input[type="text"]:focus,
.form-login input[type="text"]:hover,
.form-login input[type="password"]:focus,
.form-login input[type="password"]:hover {
  background-color: #eee;
  background-color: var(--off-white);
}
.form-login input[type="submit"] {
  background-color: #00b9bc;
  color: #eee;
  color: var(--off-white);
  font-weight: bold;
  text-transform: uppercase;
}
.form-login input[type="submit"]:focus,
.form-login input[type="submit"]:hover {
  background-color: #197071;
}
.form-field {
  display: flex;
  margin-bottom: 2rem;
}

.hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.text--center {
  text-align: center;
}

img.avatar {
  width: 40%;
  border-radius: 50%;
}

.imgcontainer {
  text-align: center;
  margin: 1.5rem 0 0.75rem 0;
  position: relative;
}

:root {
  --header-height: 0px;

}
.home__container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

:root {
  --search-box-width: 500px;
  --white-color: #fff;
}

.search-box {
  display: flex;
}

.nav-bar--small-screen {
  color: #fff;
  color: var(--white-color);
}

@media screen and (min-width: 786px) {
  .search-box {
    top: 0.625rem;
    left: 50%;
    width: 500px;
    width: var(--search-box-width);
    position: absolute;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    display: flex;
  }
  .nav-bar--small-screen {
    display: none;
  }
}

@media screen and (max-width: 786px) {
  .search-box {
    margin-top: 0.5rem;
  }

  .nav-icon--right {
    margin-top: 0.5rem;
    display: none;
  }
  .nav-bar--small-screen {
    display: block;
  }
}

.header-wrapper {
  color: #000;
}

.header-form-control {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}

.search-icon-wrapper {
  border: none;
  border-top-left-radius: 0;
  background-color: #28b8b0;
  border-bottom-left-radius: 0;
}

.pointer-margin-right-20 {
  cursor: pointer;
  margin-right: 1.25rem;
}

.cursor-pointer {
  cursor: pointer;
}

.nav-bar-icons--responsive {
  cursor: pointer;
  margin-top: 0.5rem;
}

.container__landing {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.logo__image {
  width: 20%;
}

:root {
  --fallback-color: #16222a;
  --gradient-color: #3a6073;
}

.employee__main {
  /* Remember to use the other versions for IE 10 and older browsers! */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "lato", sans-serif;
  color: #000;
  background: #222222;
  background: #16222a;
  background: var(--fallback-color); /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to top,
    #16222a,
    #3a6073);
  background: linear-gradient(
    to top,
    var(--fallback-color),
    var(--gradient-color)
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}



.employee__form {
  display: flex;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.employee__field {
  margin: 1rem;
  width: 70%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

input.employee__field_input {
  height: 3rem;
  width: 100%;
  padding-left: 5px;
  padding-right: 5px;
  margin-left: 5px;
  margin-right: 5px;
}

.employee__button {
  background-color: #00b9bc;
  color: var(--off-white);
  font-weight: bold;
  text-transform: uppercase;
}
.employee__button :hover {
  background-color: #197071;
}
.error {
  color: #ff0000;
  font-size: 0.6rem;
  position: absolute;
}

.page-error {
  width: 100%;
  height: 50vh;
  display: flex;
  padding: 32px;
  margin: 0 auto;
  text-align: center;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  background-color: #fff;
}

.loader-wrapper {
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.inline-loader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
}

